body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --peru-red: #D91023;
  --peru-yellow: #FCD116;
  --peru-blue: #007EA8;
}

#app {
  min-height: 100vh;
  overflow-x: hidden;
  background: rgb(217,16,35);
  background: #D91023;
  background: var(--peru-red);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

#nav-bar-top {
  width: 100%;
  z-index: 9999;
}

#nav-bar-top-inner {
  width: 100%;
  padding: 1em;
  text-align: center;
}

#nav-bar-top-inner h1 {
  display: inline-block;
}

#nav-bar {
  position: fixed;
  z-index: 9999;
  top: 0;
  left: 0;
  width: 100%;
  background: seashell;
  box-shadow: 0px 2px 4px 0px rgb(0, 0, 0, 0.30);
}

#nav-bar-inner {
  margin: 0.5em auto;
  width: 100%;
  max-width: 1200px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
}

#peru-coat-of-arms {
  width: auto;
  height: 3em;
  margin-left: 1em;
  margin-right: 3em;
}

#search-bar-outer {
  -webkit-flex-grow: 1;
          flex-grow: 1;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex;
  margin-right: 1em;
}

#search-bar {
  width: 100%;
  max-width: 50em;
}

#search-bar.ui.input {
  color: #007EA8;
  color: var(--peru-blue)
}

#search-input {
  border-radius: 3em;
  box-shadow: inset 0px 1px 2px 0px rgb(0, 0, 0, 0.30);
}

#content-outer {
  margin-top: 10em;
}

#results-list {
  
}

.results-list {
  -webkit-animation: results-list-in 2s;
          animation: results-list-in 2s;
}

@-webkit-keyframes results-list-in {
  from {
    padding-top: 3em;
  }

  to {
    padding-top: 0;
  }
}

@keyframes results-list-in {
  from {
    padding-top: 3em;
  }

  to {
    padding-top: 0;
  }
}
:root {
  --peru-red: #D91023;
  --peru-yellow: #FCD116;
  --peru-blue: #007EA8;
}

.word-card {
	background-color: white;
	margin: 1em 1em 1em 1em;
	padding: 2em 3em;
	border-radius: 1em;
	border: 2px solid #FCD116;
	border: 2px solid var(--peru-yellow) ;
	width: 40em;
	max-width: 95vw;
	-webkit-animation: word-card-in 3s ease-out;
	        animation: word-card-in 3s ease-out;
	box-shadow: 0px 1px 2px 0px rgb(0, 0, 0, 0.30);
}

.content h2 {
	color: #007EA8;
	color: var(--peru-blue);
}

@-webkit-keyframes word-card-in {
  from {
    opacity: 0;
    margin: 0 1em 1em 1em;
  }

  50% {
  	opacity: 1;
  }

  to {
    margin: 1em 1em 1em 1em;
  }
}

@keyframes word-card-in {
  from {
    opacity: 0;
    margin: 0 1em 1em 1em;
  }

  50% {
  	opacity: 1;
  }

  to {
    margin: 1em 1em 1em 1em;
  }
}


